.section-cta {
    padding: 0;
    color: #fff;
    background-color: $color-green;
}

.cta {
    position: relative;
    padding: 148px 0;

    @media (max-width: 991px) {
        padding: 95px 0 130px;
    }

    .shapes {
        &::before {
            content: "";
            position: absolute;
            top: 0;
            left: 0;
            width: 321.5px;
            height: 6.5px;
            background-color: $color-orange;

            @media (max-width: 991px) {
                left: -36px;
            }
        }
    }

    .barcode {
        position: absolute;
        bottom: 0;
        right: 0;
        width: 321.5px;
        height: 6.5px;
        display: flex;
        align-items: flex-start;

        img {
            @include imgfit;
        }
    }

    .row {
        margin-left: -70px;
        margin-right: -70px;

        @media (max-width: 991px) {
            max-width: 320px;

            @include margin-auto;
        }

        [class^="col-"] {
            padding-left: 70px;
            padding-right: 70px;

            @media (max-width: 991px) {
                padding-left: 0;
                padding-right: 0;
            }

            &:first-child {
                position: relative;

                &::before {
                    content: "";
                    position: absolute;
                    top: 0;
                    right: 0;
                    width: 1px;
                    height: 100%;
                    background-color: #fff;

                    @media (max-width: 991px) {
                        display: none;
                    }
                }
            }

            &:last-child {
                padding-top: 22px;
            }
        }
    }

    .logo {
        width: 150px;
        margin-bottom: 15px;

        img {
            width: 100%;
        }
    }

    .banner-title {
        width: 510px;

        .circ {
            width: 160.5px;
            height: 160.5px;
            left: -53px;
            bottom: 9px;
        }
    }

    .title-divider {
        margin: 48px 0 21px;

        &::before {
            background-color: #fff;
        }
    }

    .text-events {
        .location {
            margin-bottom: 9px;

            h2 {
                margin-bottom: 0;

                @media (max-width: 991px) {
                    font-size: 35px;
                }
            }
        }

        .time {

            .day,
            .hour {
                font-size: 28px;
                line-height: 35px;

                @media (max-width: 991px) {
                    font-size: 21px;
                }
            }

            .day {
                margin-bottom: 0;
            }

            .hour {
                margin-bottom: 3px;
            }
        }

        &-btn {
            margin-left: 0;
        }
    }
}