.footer {
    position: relative;
    text-align: center;
    color: #fff;
    padding-top: 100px;
    overflow: hidden;

    @media (max-width: 991px) {
        padding-top: 45px;
    }

    .inner {
        position: relative;
        padding: 100px 0 43px;

        @media (max-width: 991px) {
            padding: 62px 0 38px;
        }
    }

    .section-bg {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;

        img {
            @include imgfit;
        }
    }

    .circ {
        position: absolute;
        top: 0;
        left: 50%;
        transform: translateX(-50%);
        width: 841px;

        @media (max-width: 991px) {
            width: 443px;
        }

        img {
            width: 100%;
            animation: rotate 50s linear infinite;
        }
    }

    .logo {
        width: 161px;

        @include margin-auto;

        img {
            width: 100%;
        }
    }

    .copyright {
        margin-top: 55px;

        @media (max-width: 991px) {
            margin-top: 38px;
        }

        p {
            margin-bottom: 0;

            @media (max-width: 991px) {
                font-size: 14px;
            }
        }

        .term {
            font-size: 1.3rem;
            line-height: 1.2;
        }
    }
}
